import { Injectable, Injector } from '@angular/core';
import { Observable } from 'rxjs';

import { MetadataUtil, Entity, AppContext } from '@farris/devkit';

import { NG_VARIABLE } from './decorators';
import { DeprecatedBefRepository } from './deprecated/deprecated_bef_repository';
import { BefEntityManager } from './bef_entity_manager';
import { BefVariableManager } from './bef_variable_manager';
import { BefDataService } from './bef_data_service';
import { BefProxy } from './bef_proxy';
import { BefProxyExtend } from './bef_proxy_extend';
import { BE_SERVER_URI_TOKEN } from './tokens';
import { ChangeDetail } from './types';
/**
 * BEF
 * @description
 *  \
 * apiUriapi/scm/sd/v1.0/empservice
 */
@Injectable()
abstract class BefRepository<T extends Entity> extends DeprecatedBefRepository<T> {

  private befProxy: BefProxy;
  /**
   * @member {BefProxy} proxy
   * @description BefRepositoryBefhttp
   */
  public set proxy(value) {
    this.befProxy = value;
    this.befProxy.setProxyExtend(new BefProxyExtend(this));
  }
  public get proxy(): BefProxy {
    return this.befProxy;
  }
  /**
   * 
   * @internal
   */
  public entityManager: BefEntityManager<T>;

  /**
   * 
   * @internal
   */
  public variableManager: BefVariableManager;

  public appContext: AppContext;
  /**
   * 
   * @internal
   */
  protected dataService: BefDataService<T>;

  public serverUri: string;
  /**
   * @param injector 
   */
  constructor(private injector: Injector) {
    super();

    if (this.entityCollection) {
      // 
      this.entityManager = new BefEntityManager(this.entityCollection);
    }

    // 
    this.appContext = this.injector.get<AppContext>(AppContext);
    const ngVariables = MetadataUtil.getPropsMetadatasByName(this.constructor, NG_VARIABLE);
    this.variableManager = new BefVariableManager(this.appContext, ngVariables, this.injector);

    // DataService
    this.dataService = new BefDataService(this.injector, this);
    this.serverUri = injector.get<string>(BE_SERVER_URI_TOKEN);
  }

  /**
   * 
   * @param filter 
   * @param sort 
   * @param pageSize 
   * @param pageIndex 
   * @description
   * 
   * ```json
   * [
   *   {
   *     "FilterField": "DomainID.DomainID_name",
   *     "Compare": 0,
   *     "Value": "",
   *     "Relation": 1,
   *     "Expresstype": 0,
   *     "Lbracket": "(",
   *     "Rbracket": ")"
   *   }, {
   *     "FilterField": "BillDate",
   *     "Compare": 3,
   *     "Value": "2021-09-03",
   *     "Relation": 1,
   *     "Expresstype": 0
   *   }, {
   *     "FilterField": "BillDate",
   *     "Compare": 5,
   *     "Value": "2021-10-05",
   *     "Relation": 1,
   *     "Expresstype": 0
   *   }
   * ]
   * ```
   * 
   * ```typescript
   * // 
   * const query$ = this.repository.getEntities(JSON.parse(filter), JSON.parse(sort), null, null);
   * ```
   */
  public getEntities(filter: any[], sort: any[], pageSize: number | null, pageIndex: number | null): Observable<T[]> {
    return this.dataService.getList(filter, sort, pageSize, pageIndex);
  }
  /**
   * 
   * @param filter 
   * @param sorts 
   * @param pageSize 
   * @param pageIndex 
   * @description
   * getEntities \
   * bodygetEntitiesurl
   * @see {@link getEntities}
   */
  public filter(filter: any[] = [], sorts: any[] = [], pageSize: number | null, pageIndex: number | null): Observable<T[]> {
    return this.dataService.filter(filter, sorts, pageSize, pageIndex);
  }
  // #endregion
  /**
   * /
   * @param paths 
   * @param pageIndex 
   * @param pageSize 
   * @param filter 
   * @param sort 
   * @todo 
   * @description
   * ['id:', '', 'id:', ''] \
   * "id" \
   * ['id:Ted', 'accounts', 'id:10816', 'logs'] accountslogs \
   * paths \
   * 
   * ```typescript
   * // Entity
   * const fullPaths = EntityPathConverter.toEntityPathArray(this.viewModel.bindingPath, this.bindingData);
   * const paths = fullPaths.slice(0, fullPaths.length - 1);
   * const bindingPaths = this.viewModel.bindingPath.split('/').filter(item => item);
   * const bindingData = this.viewModel.bindingData;
   * let nodeCode = bindingPaths[bindingPaths.length - 1];
   * nodeCode = nodeCode.substr(0, nodeCode.length - 1);
   * // 
   * const parentPaths = bindingPaths.slice(0, bindingPaths.length - 1);
   * const parentObject = bindingData.getValue(parentPaths) as BindingObject;
   * if (!parentObject) {
   *   throw new Error('');
   * }
   * const configPath = `/${nodeCode}_${parentObject[parentObject.primaryKey]}`;
   * // 
   * const { pageIndex = 1, pageSize } = this.repository.entityCollection.getPaginationConfigByPath(configPath);
   * const request$ = this.repository.queryChild(paths, pageIndex, pageSize);
   * ```
   */
  public queryChild(paths: string[], pageIndex: number, pageSize: number, filter?: string, sort?: string) {
    return this.dataService.queryChild(paths, pageIndex, pageSize, filter, sort);
  }
  /**
  * id
  * @param id 
  * @description
  *  \
  * id {@link updateEntityById} 
  */
  public getEntityById(id: string): Observable<T> {
    return this.dataService.getById(id);
  }

  /**
   * 
   * @param id 
   * @description
   *  \
   * 
   * ```typescript
   * const rootDataId = this.bindingData.list.currentId;
   * return this.repository.updateEntityById(rootDataId);
   * ```
   */
  public updateEntityById(id: string): Observable<T> {
    return this.dataService.updateById(id);
  }
  /**
   * 
   * @param id 
   * @description
   * 
   * ```typescript
   * id = this.bindingData.list.currentId;  // 
   * const edit$ = this.repository.editEntityById(id);
   * ```
   */
  public editEntityById(id: string): Observable<T> {
    return this.dataService.editById(id);
  }


  /**
   * 
   * @param defaultValue JSON
   * @description
   *  \
   * 
   * ```typescript
   * // 
   * const create$ = this.repository.createEntity();
   * create$.pipe(
   *   tap(() => {
   *       EditStateUtil.setEditState(this.frameContext, lastModifiedId);
   *   })
   * );
   * ```
   */
  public createEntity(defaultValue?: any): Observable<T> {
    return this.dataService.create(defaultValue);
  }

  /**
   * 
   * @param defaultValue 
   * @description 
   */
  public appendEntity(defaultValue?: any): Observable<T> {
    return this.dataService.append(defaultValue);
  }

  /**
   * 
   * @param position 
   * @param defaultValue 
   * @description
   * 1-11-1
   */
  public insert(position: 1 | -1, defaultValue?: any): Observable<any> {
    return this.dataService.insert(position, defaultValue);
  }
  /**
   * 
   * @param path 
   * @param position 
   * @description
   * "/1/edus""/1/edus/12/grades" \
   *  \
   *  \
   *  {@link insert} \
   * 
   * ```typescript
   * // bindingPathbindingData
   * const append$ = this.repository.insertByPath(path, position);
   * ```
   */
  public insertByPath(path: string, position: 1 | -1): Observable<any> {
    return this.dataService.insertByPath(path, position);
  }
  /**
   * 
   * @param fpath 
   * @description
   *  {@link insertByPath}
   */
  public appendEntityByPath(fpath: string): Observable<Entity> {
    return this.dataService.appendByPath(fpath);
  }

  /**
   * id
   * @param id 
   * @param ifSave true
   * @description
   * ifSavefalse
   */
  public removeEntityById(id: string, ifSave?: boolean): Observable<boolean> {
    return this.dataService.removeById(id, ifSave);
  }

  /**
   * id
   * @param id 
   */
  public removeEntityAndSaveById(id: string): Observable<boolean> {
    return this.dataService.removeAndSaveById(id);
  }

  /**
   * id
   * @param fpath 
   * @param id 
   * @description
   *  {@link insertByPath}
   */
  public removeEntityByPath(fpath: string, id: string): Observable<boolean> {
    return this.dataService.removeByPath(fpath, id);
  }
  /**
   * ids
   * @param ids ids
   * @param ifSave (false)
   */
  public batchRemove(ids: string[], ifSave: boolean = false): Observable<any> {
    return this.dataService.batchRemove(ids, ifSave);
  }
  /**
   * 
   * @param fPath 
   * @param ids id
   * @description
   * idsid {@link insertByPath} \
   * 
   * ```typescript
   * // idsUIState
   * const remove$ = this.repository.batchRemoveByPath(path, ids);
   * ```
   */
  public batchRemoveByPath(fPath: string, ids: string): Observable<boolean> {
    return this.dataService.batchRemoveByPath(fPath, ids);
  }
  /**
   * id
   * @param id 
   * @description
   * id \
   * ```typescript
   * const id = this.bindingData.list.currentId as string;
   * const save$ = this.repository.saveEntityById(id);  // 
   * ```
   */
  public saveEntityById(id: string): Observable<boolean> {
    return this.dataService.applyChangesById(id);
  }

  /**
   * 
   * @description
   *  {@link removeEntityById}  \
   * 
   */
  public saveEntities(): Observable<boolean> {
    return this.dataService.applyChanges();
  }

  /**
   * 
   * @description 
   */
  public cancelEntityChanges(): Observable<boolean> {
    return this.dataService.cancelChanges();
  }

  /**
   * /
   * @param path  {@link insertByPath}
   * @param defaultValues 
   * @description
   * 
   */
  public batchAppendByPath(path: string, defaultValues: Array<any>): Observable<any> {
    return this.dataService.batchAppendByPath(path, defaultValues);
  }
  /**
   * 
   * @param defaultValues 
   */
  public batchAppend(defaultValues: Array<any>): Observable<any> {
    return this.dataService.batchAppend(defaultValues);
  }
  /**
   * Repositoy
   * @description BeSessionId
   */
  public reset() {
    this.entityManager.reset();
    this.variableManager.reset();
    this.restService.sessionService.clearBeSessionId();
  }

  /**
   * Injector
   * @internal
   */
  public getInjector(): Injector {
    return this.injector;
  }
  /**
   * vo
   */
  public clearAllVariableChanges(changeDetail: ChangeDetail) {
    this.variableManager.clearChangeDetail(changeDetail);
  }

}

export { BefRepository };
