import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { switchMap, map, catchError, tap } from 'rxjs/operators';
import { HttpParams, HttpClient } from '@angular/common/http';
import { encodeUrl } from '@farris/devkit';
import { IProxyExtend, RequestInfo, ResponseInfo, ChangeDetail, RetrieveParam } from './types';
import { BefDataPathUtil } from './bef_data_path_util';
import { UriService } from './uri_service';
import { HttpHeaderUtil, HttpParamUtil, RequestInfoUtil, ResponseInfoUtil } from './utils/index';
import { HttpService } from './http_service';


const GET = 'GET';
const DELETE = 'DELETE';
const PUT = 'PUT';
const POST = 'POST';
const PATCH = 'PATCH';

/**
 * Bef
 * httphttpurimethodheaderbodyquery parameter
 * uri uriService
 */
@Injectable()
export class BefProxy {
  protected proxyExtend: IProxyExtend;
  /**
   * restful
   */
  protected baseUri: string;
  private httpService: HttpService;
  constructor(public httpClient: HttpClient, public uriService: UriService) {
    this.httpService = new HttpService(this.httpClient);
  }
  /**
   * 
   * @param baseUri 
   */
  public setBaseUri(baseUri: string): void {
    this.baseUri = baseUri;
  }
  /**
   * 
   * @param strategy 
   */
  public setProxyExtend(proxyExtend: IProxyExtend) {
    this.proxyExtend = proxyExtend;
  }
  /**
   * 
   */
  public query(entityFilter?: any): Observable<any> {
    let url = this.baseUri;
    if (entityFilter) {
      const entityFilterJson = JSON.stringify(entityFilter);
      url = `${url}?entityFilter=${entityFilterJson}`;
    }
    return this.request(url, GET);
  }

  /**
   * 
   */
  public extendQuery(entityFilter: any, requestInfo: RequestInfo): Observable<ResponseInfo> {
    let url = `${this.baseUri}/extension/query`;
    if (entityFilter) {
      let entityFilterJson = JSON.stringify(entityFilter);
      entityFilterJson = encodeUrl(entityFilterJson);
      url = `${url}?entityFilter=${entityFilterJson}`;
    }
    const body = requestInfo;
    const options = this.addBodyToOptions({}, body);
    return this.request(url, PUT, null, options, false);
  }
  /**
   * 
   * @param entityFilter 
   * @param requestInfo requestInfo
   * @description extendQuerybody
   */
  public filter(entityFilter: any, requestInfo: RequestInfo): Observable<ResponseInfo> {
    let url = `${this.baseUri}/extension/filter`;
    let body: any = { requestInfo };
    if (entityFilter) {
      body = { entityFilter, requestInfo };
    }
    const options = this.addBodyToOptions({}, body);
    return this.request(url, POST, null, options, false);
  }

  /**
   * 
   * @param id 
   * @return 
   */
  public retrieve(id: string): Observable<any> {
    const url = `${this.baseUri}/${id}`;
    return this.request(url, GET);
  }
  /**
   * @deprecated 
   */
  public serviceRetrieve(id: string, retrieveParam: any, requestInfo: RequestInfo): Observable<any> {
    const url = `${this.baseUri}/service/retrieve/${id}`;
    const options = {
      body: { retrieveParam, requestInfo }
    };
    return this.request(url, PUT, null, options);
  }
  /**
   * /
   * @param params 
   */
  public queryChild(params: any) {
    const url = `${this.baseUri}/service/querychild`;
    const options = { body: params };
    return this.request(url, PUT, null, options);
  }

  /**
   * 
   */
  public extendRetrieve(id: string, requestInfo: RequestInfo, retrieveParam?: RetrieveParam): Observable<ResponseInfo> {
    const url = `${this.baseUri}/extension/retrieve/${id}`;
    let body: any = requestInfo;
    if (retrieveParam) {
      body = { retrieveParam, requestInfo };
    }
    const options = this.addBodyToOptions({}, body);
    return this.request(url, PUT, null, options);
  }

  /**
   * 
   */
  public edit(id: string, requestInfo: RequestInfo): Observable<ResponseInfo> {
    const url = `${this.baseUri}/service/edit/${id}`;
    const body = requestInfo;
    const options = this.addBodyToOptions({}, body);
    return this.request(url, PUT, null, options);
  }

  /**
   * 
   */
  public create(defaultValue?: any, requestInfo?: RequestInfo): Observable<any> {
    const body = {
      defaultValue,
      requestInfo: requestInfo,
    };
    const options = this.addBodyToOptions({}, body);
    return this.request(this.baseUri, POST, null, options);
  }

  /**
   * 
   * @path /1/edus/1/edus/11/grades
   */
  public createByPath(fpath: string, requestInfo?: RequestInfo): Observable<any> {
    const pathUrl = BefDataPathUtil.convertPathToUrl(fpath);
    const url = `${this.baseUri}${pathUrl}`;

    const body = requestInfo;
    const options = this.addBodyToOptions({}, body);
    return this.request(url, POST, null, options);
  }

  /**
   * 
   */
  update(changeDetail: ChangeDetail, requestInfo?: RequestInfo): Observable<any> {
    const body = {
      changeDetail,
      requestInfo: requestInfo,
    };
    const options = this.addBodyToOptions({}, body);

    return this.request(this.baseUri, PATCH, null, options);
  }

  /**
   * 
   */
  public save(requestInfo?: RequestInfo): Observable<any> {
    const body = requestInfo;
    const options = this.addBodyToOptions({}, body);
    return this.request(this.baseUri, PUT, null, options);
  }

  /**
   * 
   */
  public delete(id: string): Observable<any> {
    const url = `${this.baseUri}/${id}`;
    return this.request(url, DELETE);
  }

  /**
   * 
   */
  public deleteAndSave(id: any, requestInfo: RequestInfo): Observable<ResponseInfo> {
    const url = `${this.baseUri}/service/delete/${id}`;
    const body = requestInfo;
    const options = this.addBodyToOptions({}, body);
    return this.request(url, PUT, null, options);
  }

  /**
   * 
   */
  public extendDelete(id: string, requestInfo: RequestInfo): Observable<ResponseInfo> {
    const url = `${this.baseUri}/extension/delete/${id}`;
    const body = requestInfo;
    const options = this.addBodyToOptions({}, body);
    return this.request(url, PUT, null, options);
  }

  /**
   * 
   * @param fpath /1/edus/1/edus/11/grades
   */
  public deletByPath(fpath: string, id: string): Observable<any> {
    const pathUrl = BefDataPathUtil.convertPathToUrl(fpath);
    const url = `${this.baseUri}${pathUrl}/${id}`;
    return this.request(url, DELETE);
  }

  /**
   * 
   * @param fpath /1/edus/1/edus/11/grades
   */
  public extendDeletByPath(fpath: string, id: string, requestInfo: RequestInfo): Observable<ResponseInfo> {
    const pathUrl = BefDataPathUtil.convertPathToUrl(fpath);
    const url = `${this.baseUri}/extension${pathUrl}/${id}`;
    const body = requestInfo;
    const options = this.addBodyToOptions({}, body);
    return this.request(url, PUT, null, options);
  }
  /**
   * 
   * @param fPath 'extension/{rootId}/cc/batch'
   * @param  ids id
   * @param  requestInfo 
   */
  public batchDeleteByPath(fPath: string, ids: string, requestInfo: RequestInfo): Observable<ResponseInfo> {
    const pathUrl = BefDataPathUtil.convertPathToUrl(fPath);
    const pathArray = pathUrl.split('/');
    if (pathArray.length < 3) {
      throw Error(`pathpath[${fPath}]`);
    }
    /*   const rootId = pathArray[1];

    const nameArray = [];
    pathArray.forEach((name, index) => {
      if (index > 1 ) {
        if (name.indexOf('s') !== -1) {
          nameArray.push(name.substr(0, name.length - 1));
        } else {
          nameArray.push(name);
        }
      }
    });
    const childName = nameArray.join('/');*/
    const url = `${this.baseUri}/extension${pathUrl}/batch`;
    const idsArray = ids.split(',');
    const body = {
      ids: idsArray,
      requestInfo: requestInfo
    };
    const options = this.addBodyToOptions({}, body);
    return this.request(url, PUT, null, options);
  }
  /**
   * 
   * @param ids id
   */
  public batchDelete(ids: string[]): Observable<any> {
    const params = {
      ids: ids.join(',')
    };
    return this.request(this.baseUri, DELETE, params);
  }
  /**
   * ids
   * @param ids ids
   * @param ifSave 
   */
  public extendBatchDeletion(ids: string[], requestInfo: RequestInfo): Observable<any> {
    const url = `${this.baseUri}/extension/batchdeletion`;
    const body = {
      ids: ids,
      requestInfo: requestInfo
    };
    const options = this.addBodyToOptions({}, body);
    return this.request(url, PUT, null, options);
  }

  /**
   * 
   */
  public extendBatchDelete(ids: string[], requestInfo: RequestInfo): Observable<ResponseInfo> {
    const url = `${this.baseUri}/extension/batchdelete`;
    const params = {
      ids: ids.join(',')
    };

    const body = requestInfo;
    const options = this.addBodyToOptions({}, body);
    return this.request(url, PUT, params, options);
  }


  /**
   * 
   */
  public cancel(context?: any): Observable<any> {
    const url = `${this.baseUri}/service/cancel`;
    return this.request(url, POST, null, null, false, false, false, context);
  }

  /**
   * /
   * @param path like 1/childs or 1/childs/11/childchilds
   * @param defaultValues 
   */
  public batchAppendByPath(path: string, defaultValues: Array<any>, requestInfo: RequestInfo): Observable<any> {
    const pathUrl = BefDataPathUtil.convertPathToUrl(path);
    const url = `${this.baseUri}${pathUrl}/batch`;
    const body = {
      requestInfo,
      retrieveDefaultParam: {
        defaultValues
      }
    };
    const options = this.addBodyToOptions({}, body);
    return this.request(url, POST, null, options);
  }
  /**
   * 
   * @param defaultValues 
   */
  public batchAppend(defaultValues: Array<any>, requestInfo: RequestInfo): Observable<any> {
    const url = `${this.baseUri}/batch`;;
    const body = {
      requestInfo,
      retrieveDefaultParam: {
        defaultValues
      }
    };
    const options = this.addBodyToOptions({}, body);
    return this.request(url, POST, null, options);
  }
  // tslint:disable-next-line: max-line-length
  public request(
    url: string, method: string, params?: any, options?: any,
    selfHandError?: boolean, ignoreError?: boolean, ignoreChanges?: boolean, runtimeContext?: any
  ): Observable<any> {
    options = options || {};

    // 
    if (typeof (selfHandError) === 'undefined' || typeof (selfHandError) !== 'boolean') {
      selfHandError = false;
    }

    // 
    if (typeof (ignoreError) === 'undefined' || typeof (ignoreError) !== 'boolean') {
      ignoreError = false;
    }

    // params
    if (params) {
      // const httpParams = this.buildParams(params);
      options.params = params;
    }

    // headers
    options.headers = HttpHeaderUtil.convertToHeaders(options.headers);
    // 1requestInfo;
    // 2RequestInforequestInfo
    const hasRequestInfo = this.hasRequestInfo(options);
    if (options && options.body && options.body.hasOwnProperty('RequestInfo')) {
      options.body.requestInfo = options.body.RequestInfo;
      delete options.body.RequestInfo;
    }
    return this.proxyExtend.extendHeaders(options.headers, runtimeContext).pipe(
      switchMap(headers => {
        options.headers = HttpHeaderUtil.toJson(headers);
        options.observe = 'response';
        return this.httpService.request(method, url, options);
      }),
      tap((response: any) => this.proxyExtend.parseHeaders(response)),
      map((result: any) => result.body),
      map((result: any) => {
        if (hasRequestInfo) {
          this.proxyExtend.onResponse(result, ignoreChanges, options);
          return result;
        } else {

          // bodyrequestInforeturnValue
          return this.unWrapResponseInfo(result);
        }
      }),
      catchError(error => {
        return this.proxyExtend.onError(error, selfHandError, ignoreError);
      })
    );
  }

  /**
   * httpclient
   * @param url url
   * @param method methods
   * @param options options
   */
  protected invoke(url: string, method: string, options: any): Observable<any> {
    if (options.params) {
      url = this.proxyExtend.extendUrl(url, options.params);
      options.params = null;
    }
    if (options.body) {
      options.body = this.proxyExtend.extendBody(options.body);
    }
    options.headers = HttpHeaderUtil.convertToHeaders(options.headers);
    const headers$ = this.proxyExtend.extendHeaders(options.headers);
    return headers$.pipe(
      switchMap(headers => {
        options.headers = HttpHeaderUtil.toJson(headers);
        options.observe = 'response';
        return this.httpService.request(method, url, options)
          .pipe(
            tap((response: any) => this.proxyExtend.parseHeaders(response)),
            map((result: any) => result.body),
            map((result: any) => this.proxyExtend.onResponse(result, false, options)),
            catchError(error => this.proxyExtend.onError(error, false, false))
          );
      })
    );
  }

  /**
   * HttpOptionsbody
   * @param options options
   * @param body body
   */
  private addBodyToOptions(options: any, body: any) {
    options = options || {};
    const mergedOptions = Object.assign(options, { body });
    return mergedOptions;
  }
  /**
   * HttpParams
   * @param params 
   */
  private buildParams(params: object): HttpParams {
    return HttpParamUtil.buildParams(params);
  }
  /**
   * RequestInfo
   */
  private hasRequestInfo(options: any): boolean {
    return RequestInfoUtil.hasRequestInfo(options) || RequestInfoUtil.hasRequestInfoWithR(options);
  }
  /**
   * 
   */
  private unWrapResponseInfo(result: any) {
    return ResponseInfoUtil.unWrapResponseInfo(result);
  }
}
