import { Injectable, Injector, InjectFlags, inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { FrameworkSessionService } from '../framework_session_service';
import { BE_SESSION_HANDLING_STRATEGY_TOKEN } from '../tokens';
import { BefSessionHandlingStrategy, BefSeparatedSessionHandlingStrategy } from './bef_session_handling_strategy';
import { BefSessionHandlingStrategyFactory } from './bef_session_handling_strategy_factory';
import { AppContext } from '@farris/devkit';

@Injectable()
class BefSessionService {
  public beBaseUri: string;
  /**
   * Session
   */
  private handlingStrategy: BefSessionHandlingStrategy;
  private injector: Injector;
  /**
   * 
   * @summary
   * new BeSessionService()injector
   */
  constructor(
    httpClient: HttpClient, beBaseUri: string,
    frmSessionService: FrameworkSessionService, injector?: Injector
  ) {
    this.beBaseUri = beBaseUri;
    this.injector = injector;
    this.handlingStrategy = this.createHandlingStrategy(injector, httpClient, frmSessionService, beBaseUri);
  }
  /**
   * token
   */
  public get token() {
    return this.handlingStrategy.getFrameworkSessionId();
  }

  /**
   * BeSessionId
   */
  public getBeSessionId(): Observable<string> {
    return this.handlingStrategy.getSessionId();
  }

  /**
   * sessionId
   * @param sessionId sessionId
   */
  public setBeSessionId(sessionId: string) {
    this.handlingStrategy.setSessionId(sessionId);
    this.setSessionId(sessionId);
  }

  /**
   * BeSessionId
   */
  public clearBeSessionId() {
    this.handlingStrategy.clearSessionId();
  }

  /**
   * header
   */
  public extendRequestHeaders(headers: HttpHeaders, runtimeContext?: any): HttpHeaders {
    return this.handlingStrategy.extendRequestHeaders(headers, runtimeContext);
  }

  /**
   * header
   */
  public handleResponseHeaders(headers: HttpHeaders): void {
    return this.handlingStrategy.handleReponseHeaders(headers);
  }

  /**
   * BeSessionId
   * @deprecated
   * EAPIcreateSession
   * @summary
   * 1new BeSessionService()
   * 2BefSeparatedSessionHandlingStrategy.
   */
  public createBeSession(): Observable<string> {
    const handlingStrategy = this.handlingStrategy as BefSeparatedSessionHandlingStrategy;
    return handlingStrategy.createSession();
  }
  private setSessionId(sessionId: string) {
    const appContext = this.injector.get<AppContext>(AppContext, null, InjectFlags.Optional);
    if (appContext && sessionId) {
      appContext.params.set('sessionId', sessionId);
    }
  }
  /**
   * BeSession
   * @summary session
   *
   * 1BefSessionServicesessioninjector
   *
   * 2session`injector```
   *
   * 3sessionsession
   */
  private createHandlingStrategy(
    injector: Injector, httpClient: HttpClient,
    frmSessionService: FrameworkSessionService, beBaseUri: string
  ): BefSessionHandlingStrategy {
    let handlingStrategyName: string;
    // befSessionService
    if (!injector) {
      handlingStrategyName = 'SeparatedSession';
    } else {
      // bef 
      handlingStrategyName = injector.get<string>(BE_SESSION_HANDLING_STRATEGY_TOKEN, null, InjectFlags.Optional);
    }
    // session
    if (!handlingStrategyName) {
      handlingStrategyName = 'SeparatedSession';
    }
    const handlingStrategyFactory = new BefSessionHandlingStrategyFactory();
    return handlingStrategyFactory.create(handlingStrategyName, frmSessionService, beBaseUri, httpClient, injector);
  }
}


export { BefSessionService };
