import { Entity } from '@farris/devkit';
import { BefRepository } from '../bef_repository';
import { RequestInfo } from '../types';

class RequestInfoUtil {

  /**
   * requestInfo
   */
  public static hasRequestInfo(options: any): boolean {
    if (!options || !options.body) {
      return false;
    }
    const body = options.body;

    // 
    if (body.requestInfo && body.requestInfo.dataChange) {
      return true;
    }
    if (body.dataChange) {
      return true;
    }
    return false;
  }

  /**
   * RquestInfo
   * @summary
   * RequestInforeturnValue
   */
  public static hasRequestInfoWithR(options: any): boolean {
    if (!options || !options.body) {
      return false;
    }
    const body = options.body;

    // 
    if (body.RequestInfo && body.RequestInfo.dataChange) {
      return true;
    }
    if (body.dataChange) {
      return true;
    }
    return false;
  }

  /**
   * RequestInfo
   */
  public static buildRequestInfo(repository: BefRepository<Entity>): RequestInfo {
    const requestInfo: RequestInfo = {
      dataChange: repository.entityManager.buildAllEntityChangeDetails(),
      variableChange: repository.variableManager.buildChangeDetail()
    };
    return requestInfo;
  }

  /**
   * requestInfobodybody
   * @summary
   * body=requestInfo
   */
  public static buildBodyWithRequestInfo(body: any, repository: BefRepository<Entity>): any {
    if (body.RequestInfo || body.requestInfo) {
      return body;
    }
    const requestInfo = this.buildRequestInfo(repository);
    const bodyWithRequestInfo = Object.assign({}, body, { requestInfo: requestInfo });
    return bodyWithRequestInfo;
  }
  public static getRequestInfo(options: any) {
    if (!options || !options.body) {
      return null;
    }
    const body = options.body;

    // 
    if (body.requestInfo) {
      return body.requestInfo;
    }
    if (body.dataChange || body.variableChange) {
      return body;
    }
    return null;
  }
}

export { RequestInfoUtil };
