import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { tap } from 'rxjs/operators';
import { FrameworkSessionService } from '../framework_session_service';
import { HttpHeaderUtil } from '../utils/index';
import { BeSessionStorageStrategy } from './bef_session_storage_strategy';
import { HttpService } from '../http_service';
import { Injector } from '@angular/core';
import { AppContext } from '@farris/devkit';
/**
 * BefSession
 */
abstract class BefSessionHandlingStrategy {

  /**
   * 
   */
  protected storageStrategy: BeSessionStorageStrategy;

  /**
   * Session
   */
  protected frmSessionService: FrameworkSessionService;
  /**
   * SessionId
   */
  protected getFrmSessionId(runtimeContext?: any): string {
    return this.frmSessionService.getCurrentSessionId(runtimeContext);
  }
  protected get frmSessionId(): string {
    return this.frmSessionService.getCurrentSessionId();
  }

  /**
   * 
   */
  constructor(storageStrategy: BeSessionStorageStrategy, frmSessionService: FrameworkSessionService) {
    this.storageStrategy = storageStrategy;
    this.frmSessionService = frmSessionService;
  }

  /**
   * SessionId
   */
  public abstract getSessionId(): Observable<string>;
  public abstract setSessionId(sessionId): void;
  public abstract clearSessionId(): void;
  public abstract extendRequestHeaders(headers: HttpHeaders, runtimeContext?: any): HttpHeaders;
  public abstract handleReponseHeaders(headers: HttpHeaders): void;
  protected abstract getSessionStorageKey(runtimeContext?: any): string;

  /**
   * SessionId
   */
  public getFrameworkSessionId(runtimeContext?: any) {
    return this.getFrmSessionId(runtimeContext);
  }

  /**
   * BeSession
   */
  protected getSessionIdFromStorage(runtimeContext?: any) {
    const sessionStorageKey = this.getSessionStorageKey(runtimeContext);
    const beSessionId = this.storageStrategy.getItem(sessionStorageKey);
    return beSessionId;
  }
}

/**
 * BeSessioninjectornull
 * @summary
 * ----------------------------------------
 * 
 * 1createSession
 * 2RepositoryBeSession
 * 3BEEAPIheaderSessionId
 * ----------------------------------------
 * 
 * 1new BeSessionService()
 */
class BefSeparatedSessionHandlingStrategy extends BefSessionHandlingStrategy {
  private injector: Injector;
  /**
   * SessionEAPI
   */
  private beSessionUri: string;

  /**
   * httpClient
   */
  private httpClient: HttpClient;

  private httpService: HttpService;
  /**
   * 
   */
  constructor(
    storageStrategy: BeSessionStorageStrategy, frmSessionService: FrameworkSessionService,
    httpClient: HttpClient, beBaseUri: string, injector: Injector
  ) {
    super(storageStrategy, frmSessionService);
    this.beSessionUri = beBaseUri;
    this.httpClient = httpClient;
    this.httpService = new HttpService(this.httpClient);
    this.injector = injector;
  }

  /**
   * BeSessionId
   */
  public getSessionId(): Observable<string> {
    const beSessionId = this.getSessionIdFromStorage();
    if (beSessionId) {
      return of(beSessionId);
    }
    return this.createSession();

  }

  /**
   * BeSessionId
   */
  public setSessionId(sessionId: string): void {
    const sessionKey = this.getSessionStorageKey();
    this.storageStrategy.setItem(sessionKey, sessionId);
  }

  /**
   * Sessionid
   */
  public clearSessionId() {
    const sessionKey = this.getSessionStorageKey();
    // this.storageStrategy.removeItem(sessionKey);
    this.storageStrategy.clear(this.frmSessionId, sessionKey);
  }

  /**
   * Session
   */
  public extendRequestHeaders(headers: HttpHeaders, runtimeContext?: any): HttpHeaders {
    const frmSessionId = this.getFrameworkSessionId(runtimeContext);
    const beSessionId = this.getSessionIdFromStorage(runtimeContext);
    headers = HttpHeaderUtil.appendCafRuntimeCommonVariable(headers, frmSessionId);
    if (beSessionId) {
      headers = HttpHeaderUtil.appendCafRuntimeContext(headers, beSessionId);
      headers = HttpHeaderUtil.appendSessionId(headers, beSessionId);
    }

    // const appContext = this.injector.get<AppContext>(AppContext, null);
    //if (appContext) {
    // const appId = appContext.ApplicationId;
    headers = HttpHeaderUtil.appendFuncInstId(headers, this.beSessionUri);
    // }
    // headers = HttpHeaderUtil.appendRequireMessage(headers, true);
    return headers;
  }

  /**
   * headers
   */
  public handleReponseHeaders(headers: HttpHeaders): void {
  }

  /**
   * BeSessionId
   */
  public createSession(): Observable<string> {
    const params: { [propName: string]: any } = {
      responseType: 'text'
    };
    if (!!this.frmSessionId) {
      const appContext = this.injector.get<AppContext>(AppContext, null);
      params.headers = new HttpHeaders({ SessionId: this.frmSessionId });
      params.headers = params.headers.append('X-CAF-Runtime-CommonVariable', this.frmSessionId);
      //if (appContext) {
      // const appId = appContext.ApplicationId;
      params.headers = params.headers.append('Func-Inst-Id', this.beSessionUri);
      //}
      params.headers = HttpHeaderUtil.toJson(params.headers);
    }

    return this.httpService.request('POST', this.beSessionUri, params).pipe(
      tap((beSessionId: string) => {
        this.setSessionId(beSessionId);
      })
    );
  }

  public extendHttpHeader() {
  }

  /**
   * RepositoryBeSessionkey
   * @summary
   * 1BeSessionBeSessionUri
   * 2DebugFrmSessionId=UserSessionidkey
   */
  protected getSessionStorageKey(runtimeContext?: any): string {
    let sessionId = null;
    if (runtimeContext) {
      sessionId = this.getFrameworkSessionId(runtimeContext);
    } else {
      sessionId = this.frmSessionId;
    }
    return `${sessionId}_${this.beSessionUri}`;
  }

}


class BefUnifiedSessionHandlingStrategy extends BefSessionHandlingStrategy {

  /**
   * SessionEAPI
   */
  private beSessionUri: string;
  private injector: Injector;

  /**
   * 
   */
  constructor(
    storageStrategy: BeSessionStorageStrategy, frmSessionService: FrameworkSessionService, beBaseUri: string, injector: Injector
  ) {
    super(storageStrategy, frmSessionService);
    this.beSessionUri = beBaseUri;
    this.injector = injector;
  }

  public getSessionId(): Observable<string> {
    const sessionKey = this.getSessionStorageKey();
    const sessionId = this.storageStrategy.getItem(sessionKey);
    return of(sessionId)
    // return of(null);
  }

  /**
   * BeSessionId
   */
  public setSessionId(sessionId: string): void {
    const sessionKey = this.getSessionStorageKey();
    this.storageStrategy.setItem(sessionKey, sessionId);
  }

  /**
   * Sessionid
   */
  public clearSessionId() {
    const sessionKey = this.getSessionStorageKey();
    this.storageStrategy.removeItem(sessionKey);
  }

  /**
   * Session
   */
  public extendRequestHeaders(headers: HttpHeaders, runtimeContext?: any): HttpHeaders {
    const frmSessionId = this.getFrameworkSessionId(runtimeContext);
    const beSessionId = this.getSessionIdFromStorage(runtimeContext);
    // headers = HttpHeaderUtil.appendRequireMessage(headers, true);
    const appContext = this.injector.get<AppContext>(AppContext, null);
    if (appContext) {
      const token = appContext.Token;
      headers = HttpHeaderUtil.appendFuncInstId(headers, token);
    }
    headers = HttpHeaderUtil.appendCafRuntimeCommonVariable(headers, frmSessionId);
    if (beSessionId) {
      headers = HttpHeaderUtil.appendCafRuntimeContext(headers, beSessionId);
    }
    return headers;
  }

  /**
   * headers
   */
  public handleReponseHeaders(headers: HttpHeaders): void {
  }


  /**
   * RepositoryBeSessionkey
   * @summary
   */
  protected getSessionStorageKey(runtimeContext?: any): string {
    // const isDebug = false;
    // if (isDebug) {
    //   return `${this.frmSessionId}_${this.beSessionUri}`;
    // } else {
    //   return this.frmSessionId;
    // }
    let sessionId = null;
    if (runtimeContext) {
      sessionId = this.getFrameworkSessionId(runtimeContext);
    } else {
      sessionId = this.frmSessionId;
    }
    return `${sessionId}_${this.beSessionUri}`;
  }
}

export { BefSessionHandlingStrategy, BefSeparatedSessionHandlingStrategy, BefUnifiedSessionHandlingStrategy };
