import {
  BindingData, BindingPropertyType, PropertyUtil, BindingPathConverter
} from '@farris/devkit';

/**
 * BefPath
 */
class BefDataPathUtil {

  /**
   * 
   * 
   * /[ parentId ]
   * /childCodes[ parentId, childCodes, childId ]
   * /childCodes/grandCodes [ parentId, childCode, childId, grandCode, grandId ]
   */
  public static convertToPathArray(bindingPath: string | string[], bindingData: BindingData): string[] {
    const bindingPathArray: string[] = BindingPathConverter.toBindingPathArray(bindingPath);
    const pathArray: string[] = [];

    let currentBindingObject = bindingData.list.currentItem;
    pathArray.push(currentBindingObject.primaryKeyValue);

    bindingPathArray.forEach((propName: string) => {
      const propInfo = PropertyUtil.getPropertyByName(currentBindingObject.properties, propName);
      if (propInfo.type !== BindingPropertyType.List) {
        throw new Error(`${propInfo.name}`);
      }
      const currentBindingList = currentBindingObject[propName];
      currentBindingObject = currentBindingList.currentItem;
      pathArray.push(this.trimTrailingS(propName));
      pathArray.push(currentBindingObject.primaryKeyValue);
    });

    return pathArray;
  }

  /**
   * RestUrl
   *
   * 
   * //
   * /jiwtEdus/xxx/jiwtEdu
   * /jiwtEdus/jiwtGrades /xxx/jiwtEdu/xxx/jiwtGrade
   */
  public static convertToPathUrl(bindingPath: string | string[], bindingData: BindingData): string {
    const pathArray = this.convertToPathArray(bindingPath, bindingData);
    pathArray.pop();
    return '/' + pathArray.join('/');
  }

  /**
   * 
   * 
   * /[]
   * /childCodes[ childCodes ]
   * /childCodes/grandCodes [childCode ,grandCode]
   */
  public static convertToObjectCodes(bindingPath: string | string[], bindingData: BindingData) {
    const pathArray = this.convertToPathArray(bindingPath, bindingData);
    const pathLen = pathArray.length;

    const objectCodes = [];
    for (let i = 1; i < pathLen; i = i + 2) {
      objectCodes.push(pathArray[i]);
    }

    return objectCodes;
  }

  /**
   * id
   * /[ parentId ]
   * /childCodes[ parentId, childId ]
   * /childCodes/grandCodes [ parentId, childId, grandId]
   */
  public static convertToDataIdsForUpdate(bindingPath: string | string[], bindingData: BindingData) {
    const pathArray = this.convertToPathArray(bindingPath, bindingData);
    const pathLen = pathArray.length;

    const dataIds = [];
    for (let i = 0; i < pathLen; i = i + 2) {
      dataIds.push(pathArray[i]);
    }

    return dataIds;
  }

  /**
   * id
   * /[ ]
   * /childCodes[ parentId ]
   * /childCodes/grandCodes [ parentId, childId]
   */
  public static convertToDataIdsForAdd(bindingPath: string | string[], bindingData: BindingData) {
    const dataIds = this.convertToDataIdsForUpdate(bindingPath, bindingData);
    dataIds.pop();
    return dataIds;
  }

  /**
   * EntityPathStringUrl
   * @deprecated
   */
  public static convertPathToUrl(path: string): string {
    const subPaths = path.split('/');
    for (let i = subPaths.length - 1; i > 0; i--) {
      if (subPaths[i] && subPaths[i].endsWith('s')) {
        subPaths[i] = subPaths[i].substr(0, subPaths[i].length - 1).toLowerCase();
      }
    }
    // url
    return subPaths.join('/'); // .toLowerCase();
  }

  /**
   * s
   */
  private static trimTrailingS(str: string) {
    return str.substr(0, str.length - 1);
  }
  /**
   * 
   *
   * 1.['id:xxx','child1s','id:xx','prop'] => ['child1s','prop']  
   * 2.['prop1','prop2','prop3'] => ['prop1','prop2','prop3']  
   * 3.['prop1','id:xxx','prop2'] => ['prop1','prop2']  
   * @param paths :['id:xxx','child1s','id:xx','prop']
   */
  public static convertPathsToNodeCodes(paths: string[]) {
    const result = [];
    if (!paths || paths.length < 1) {
      return result;
    }
    paths.filter(item => !!item && item.indexOf(':') === -1).forEach(node => {
      result.push(node);
    });
    return result;
  }
  /**
   * 
   * @param paths :['id:xxx','child1s','id:xx','prop'] => ['xxx','xx']
   */
  public static convertPathsToIds(paths: string[]) {
    const result = [];
    if (!paths || paths.length < 1) {
      return result;
    }
    paths.filter(item => !!item && item.indexOf(':') !== -1).forEach(id => {
      result.push(id.split(':')[1]);
    });
    return result;
  }
}

export { BefDataPathUtil };
