import { makePropDecorator } from '@farris/devkit';


/**
 * 
 */
export const NG_VARIABLE = '@farris/bef NG_VARIABLE';


/**
 * NgVariable
 */
export interface NgVariable {

  /**
   * 
   */
  mapping: string;
  /**
   * 
   */
  originalDataType?: string;
  /**
   * locale|remote
   */
  category?: string;
}


/**
 * NgVariable
 */
export interface NgVariableDecorator {
  (obj?: NgVariable): any;
  new(obj?: NgVariable): any;
}


/**
 * NgVariable
 */
export const NgVariable: NgVariableDecorator =
  makePropDecorator(NG_VARIABLE, (obj: NgVariable) => obj);
