/**
 * BeSession
 */
interface BeSessionStorageStrategy {
  getItem(beSessionKey: string): string;
  setItem(beSessionKey: string, beSessionid: string): void;
  removeItem(beSessionKey: string): void;
  clear(frmSessionId: string, beSessionKey: string): void;
}

/**
 * SessionStorageBeSession
 * @summary
 * 1SeparatedSession
 *  {
 *    BE_SESSION_ID: {
 *      frmSessionId1_beSessionUri1: beSessionId-1,
 *      frmSessionId2_beSessionUri2: beSessionId-2,
 *    }
 *  }
 * 2UnifiedSessionDebug1
 * {
 *    BE_SESSION_ID: {
 *      frmSessionId1: beSessionId-1,
 *      frmSessionId2: beSessionId-2,
 *    }
 *  }
 */
class SessionStorageBeSessionStorageStrategy implements BeSessionStorageStrategy {

  /**
   * Token
   */
  private sessionStorageKey = 'BE_SESSION_ID';

  /**
   * 
   */
  public getItem(beSessionKey: string): string {
    const beSessions = this.getAllBeSessions();
    return beSessions[beSessionKey];
  }

  /**
   * 
   */
  public setItem(beSessionKey: string, beSessionId: string) {
    const beSessions = this.getAllBeSessions();
    beSessions[beSessionKey] = beSessionId;
    this.setAllBeSessions(beSessions);
  }

  /**
   * 
   */
  public removeItem(beSessionKey: string) {
    const beSessions = this.getAllBeSessions();
    if (beSessions[beSessionKey]) {
      delete beSessions[beSessionKey];
    }
    this.setAllBeSessions(beSessions);
  }
  /**
   * Session
   * @summary
   * tokenBeSession
   * beSessionUriSessionid
   */
  public clear(frmSessionId: string, beSessionUri: string): void {
    const isInFramework = this.isInFramework();
    if (isInFramework === true) {
      this.removeItemsByFrmSessionId(frmSessionId);
    } else {
      this.removeItem(beSessionUri);
    }
  }

  /**
   * frmSessionIdBeSessionId
   */
  private removeItemsByFrmSessionId(frmSessionId: string) {
    const beSessions = this.getAllBeSessions();
    Object.keys(beSessions).forEach((beSessionKey: string) => {
      if (beSessionKey.startsWith(frmSessionId) === true) {
        delete beSessions[beSessionKey];
      }
    });
    this.setAllBeSessions(beSessions);
  }
  /**
   * SessionStorageBeSessions
   */
  private getAllBeSessions(): any {
    const beSessionsJson = window.sessionStorage.getItem(this.sessionStorageKey);
    if (!beSessionsJson) {
      return {};
    }
    return JSON.parse(beSessionsJson);
  }

  /**
   * BeSessionsSessionStorage
   */
  setAllBeSessions(beSessions: any): void {
    const beSessionsString = JSON.stringify(beSessions);
    window.sessionStorage.setItem(this.sessionStorageKey, beSessionsString);
  }
  /**
   * 
   */
  private isInFramework(): boolean {
    const hashString = window.location.hash;
    if (!hashString) {
      return false;
    }
    // todo: url
    return hashString.indexOf('tabId=') !== -1;
  }

}

export { BeSessionStorageStrategy, SessionStorageBeSessionStorageStrategy };

