import { Entity, EntityList, EntityFactory, EntityCollection, EntityManager } from '@farris/devkit';
import { ChangeDetail} from './types';
import { EntityUtil } from './entity_util';
import { BefChangeBuilder } from './bef_change_builder';
import { BefChangeHandler } from './bef_change_handler';


/**
 * Entity
 * @todo
 * 1Devkit
 * 2
 */
class BefEntityManager<T extends Entity> extends EntityManager<T> {


  /**
   * ChangeDetail
   */
  public changeBuilder: BefChangeBuilder;

  /**
   * ChangeDetail
   */
  public changeHandler: BefChangeHandler;

  /**
   * 
   */
  constructor(entityCollection: EntityCollection<T>) {
    super(entityCollection);
    this.changeBuilder = new BefChangeBuilder(this.entityCollection.entityType, this.entityCollection);
    this.changeHandler = new BefChangeHandler();
  }


  // #region ChangeDetail


  /**
   * 
   */
  public buildAllEntityChangeDetails(): ChangeDetail[] {
    const changeDetails: ChangeDetail[] = [];
    const entities = this.entityCollection.getAllEntities();
    entities.forEach((entity: Entity) => {
      if (entity.changes.length === 0) {
        return;
      }
      const changeDetail = this.changeBuilder.build(entity.changes);
      
      // id
      // @todoBug
      if (changeDetail && changeDetail.ChangeInfo && changeDetail.ChangeInfo.DataId) {
        changeDetails.push(changeDetail);
      }
    });
    return changeDetails;
  }

  /**
   * 
   */
  public buildEntityChangeDetailById(id: string): ChangeDetail {
    const entity = this.entityCollection.getEntityById(id);
    if (entity.changes.length === 0) {
      return null;
    }
    const changeDetail = this.changeBuilder.build(entity.changes);
    return changeDetail;
  }

  /**
   * 
   * @param changeDetails ChangeDetail
   */
  public handleDataChangeDetails(changeDetails: ChangeDetail[]): void {
    this.changeHandler.handle(this.entityType, this.entityCollection, changeDetails);
  }

  // #endregion

  /**
   * 
   */
  public reset() {
    this.entityCollection.clear();
  }

}

export { BefEntityManager };
